@import 'libs/normalize.scss';
body {
  background-color: #fafafa;
}
.todo {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  width: 530px;
  height: 600px;
  background-color: #fff;
  box-shadow: 0px 9px 24px rgba(0, 0, 0, 0.01), 0px 25px 40px rgba(0, 0, 0, 0.05);
  border-radius: 10px;
  overflow: hidden;
  border: 1px solid #ebebeb;

}
.todo2 {
  position: absolute;
  left: 50%;
  top: 73%;
  transform: translate(-50%, -50%);
  width: 530px;
  height: 600px;
  background-color: #fff;
  box-shadow: 0px 9px 24px rgba(0, 0, 0, 0.01), 0px 25px 40px rgba(0, 0, 0, 0.05);
  border-radius: 10px;
  overflow: hidden;
  border: 1px solid #ebebeb;
  overflow-y: scroll;
  -ms-overflow-style: none;
}
.todo__header {
  background-color: #db4c3f;
  padding: 20px 30px;
}
.todo__header h4 {
  color: #fff;
  font-size: 22px;
  font-weight: 500;
}
.todo__add-field {
  display: flex;
  border-bottom: 1px solid #f4f4f4;
}
.todo__add-field input {
  border: 0;
  font-size: 18px;
  padding: 20px;
  flex: 1;
}
.todo__add-field input::-webkit-input-placeholder {
  color: #bfbfbf;
}
.todo__add-field input:-ms-input-placeholder {
  color: #bfbfbf;
}
.todo__add-field input::-ms-input-placeholder {
  color: #bfbfbf;
}
.todo__add-field input::placeholder {
  color: #bfbfbf;
}
.todo__add-field-button {
  display: flex;
  align-items: center;
  justify-content: center;
  border: 0;
  background-color: transparent;
  width: 65px;
  height: 65px;
  cursor: pointer;
}
.todo__add-field-button svg {
  position: relative;
  right: -1px;
}
.todo__add-field-button:hover {
  background-color: #fbfbfb;
}
.todo__add-field-button:hover svg path {
  fill: #202020;
}
.todo__list{
  overflow-y: scroll;
}
.todo__list-item {
  display: flex;
  padding: 20px;
  border-bottom: 1px solid #f4f4f4;
}
.todo__list-item--completed {
  text-decoration: line-through;
}
.todo__list-item--completed .todo__list-item-check svg {
  display: block;
}
.todo__list-item--completed .todo__list-item-check, .todo__list-item--completed .todo__list-item-check:hover {
  background-color: #db4c3f;
}
.todo__list-item-check {
  display: flex;
  align-items: center;
  justify-content: center;
  border: 2px solid #db4c3f;
  border-radius: 30px;
  height: 24px;
  width: 24px;
  margin-right: 15px;
  cursor: pointer;
}
.todo__list-item-check svg {
  display: none;
}
.todo__list-item-check:hover {
  background-color: #f8e0de;
}
.todo__list-item-remove:hover {
  cursor: pointer;
}
.todo__list-item-remove:hover svg path {
  fill: #202020;
}
.todo__list-item p {
  font-size: 18px;
  flex: 1;
}